*{
    font-family: 'Montserrat', sans-serif;
}
.modal-wrapper_lead{
    background: rgba(0,0,0, 0.5);
    color: #fff;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal-wrapper_lead .box{
    width: 80%;
    max-width: 1000px;
    height: 360px;
    background: #000;
    color: #fff;
    display: flex;
    align-items: center;
    flex-direction: row;
    position: relative;
}
.cancel{
    width: 16px;
    padding: 0;
    position: absolute;
    background: transparent;
    border: none;
    top: 5px;
    z-index: 999;
    right: 5px;
}
.cancel img{
    width: 100%;
}
.box_left{
    width: 30%;
    flex: 1;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffceca;
    color: #212121;
    
    letter-spacing: 3px;
    font-size: 2em;
}
.box_right{
    width: 70%;
    background: #fff;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.modal-wrapper_lead .box h2{
    font-size: 20px;
    
    margin-bottom: 10px;
    color: #212121;
}
.box-content{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.box-content label{
    color: #212121;
    font-size: 0.8rem;
}
.box-content input{
    flex: 1;
    height: 2.5em;
}
.box-content .form-group{
    margin-bottom: 0;
}
.box-content button{
    background-color: #212121;
    text-transform: uppercase;
    margin-top: 10px;
}
#name{ 
    margin-right: 10px;
}

.flex-center{
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal-message{
    position: absolute;
    z-index: 99;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    color: #fff;
}